@import url('https://fonts.googleapis.com/css?family=Ubuntu');

:root {
  --primary-color: #737cdd;
  --secondary-color: #373b69;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: Ubuntu;
  background-color: var(--primary-color);

}

main {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;

}

header {
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  background-color: var(--secondary-color);
  align-items: center;
  padding: 10px 70px;

}

.logo {
  display: block;
}


header .logo h1>a {
  font-weight: bold;
  font-size: 40px;
  cursor: pointer;
  text-decoration: none;
  color: rgb(98, 133, 15);
  padding: 10px 15px;
  border: 4px solid rgb(98, 133, 15);
  border-radius: 8px;
  box-shadow: 5px 5px 8px rgb(82, 81, 49);

}

.search {
  display: flex;
  background-color: transparent;
  border: 2px solid var(--primary-color);
  color: white;
  border-radius: 50px;
  font-size: 1rem;
  padding: 1rem;
  font-family: inherit;
  cursor: pointer;
  letter-spacing: 1px;
}

.search::placeholder {
  color: black;
  font-weight: bold;
  font-size: 18px;
}

.search:focus {
  outline: none;
  background-color: var(--primary-color);
}