.movie {
    width: 300px;
    margin: 1rem;
    background-color: var(--primary-color);
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    position: relative;
    overflow: hidden;
    border-radius: 4px;
    border-radius: 10px;
    cursor: pointer;

}

.movie img {
    width: 100%;
    transition: 0.4s ease-in-out;
}

.movie-info {
    color: whitesmoke;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0.5rem 1rem 1rem;
    letter-spacing: .5px;
}

.movie-info h3 {
    margin-top: 0;
}

.movie-info span {
    background-color: var(--secondary-color);
    padding: 0.3rem;
    border-radius: 10%;
    font-weight: bolder;
}

.movie-info span.green {
    color: lightgreen;
}

.movie-info span.orange {
    color: orange;
}

.movie-info span.red {
    color: red;
}

.movie-info span {
    background-color: var(--secondary-color);
    padding: 0.3rem;
    border-radius: 10%;
    font-weight: bolder;
}

.overview {
    background-color: rgb(200, 200, 200);
    padding: 1.2rem;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    text-align: center;
    max-height: 60%;
    transform: translateY(400px);
    opacity: 0.9;
    transition: 0.4s;
    border-radius: 10px;

}

.movie:hover .overview {
    transform: translateY(0);

}

.movie:hover img {
    transform: scale(1.05);
}